import React, { useEffect } from 'react';
import { useNavigate } from 'react-router-dom';


function Tokens() {
  const navigate = useNavigate();

  useEffect(() => {
    const script = document.createElement('script');
    script.src = 'https://s3.tradingview.com/external-embedding/embed-widget-ticker-tape.js'
    script.async = true;
    script.InnerHTML = `
      {
        "symbols": [
          {
            "description": "",
            "proName": "BINANCE:BTCUSDT"
          },
          {
            "description": "",
            "proName": "BINANCE:ETHUSDT"
          },
          {
            "description": "",
            "proName": "CRYPTOCAP:USDT"
          },
          {
            "description": "",
            "proName": "CRYPTOCAP:USDC"
          },
          {
            "description": "",
            "proName": "CRYPTOCAP:ADA"
          },
          {
            "description": "",
            "proName": "CRYPTOCAP:MATIC"
          },
          {
            "description": "",
            "proName": "CRYPTOCAP:SOL"
          },
          {
            "description": "",
            "proName": "CRYPTOCAP:BNB"
          },
          {
            "description": "",
            "proName": "BINANCE:ARBUSDT"
          },
          {
            "description": "",
            "proName": "CRYPTOCAP:AAVE"
          },
          {
            "description": "",
            "proName": "CRYPTOCAP:UNI"
          }
        ],
        "showSymbolLogo": true,
        "colorTheme": "light",
        "isTransparent": true,
        "displayMode": "adaptive",
        "locale": "en"
      } 
    }
    `;
    document.body.appendChild(script);

    return () => {
      document.body.removeChild(script);
      const widgetContainer = document.querySelector('.tradingview-widget-container');
      if (widgetContainer) {
        while (widgetContainer.firstChild) {
          widgetContainer.removeChild(widgetContainer.firstChild);
        }
      }
    };
  }, []);

  const handleGoBack = () => {
    navigate(); // Go back to the previous page
  };

  return (
    <div>
    <div className="tradingview-widget-container">
      <div className="tradingview-widget-container__widget"></div>
      <div className="tradingview-widget-copyright">        
      </div>
        <button onClick={handleGoBack}>Go Back</button>
      </div>
    </div>
  );
}

export default Tokens;
